import React, {Component} from 'react';
import { connect } from "react-redux";

import MeijuList from '../../components/meiju_list/meiju_list';
import { getMeijuList } from "../../redux/actions";

import { Spin } from "antd";

import './recommend.less';

let isLoading = true;

class Recommend extends Component {

    componentDidMount () {
        isLoading = true;
        this.props.getMeijuList('/api/recommend');
        isLoading = false;
    }

    render() {
        return (
            <div className='list-container'>
                <Spin className='spin' spinning={isLoading} size="large" />
                <MeijuList meijuList={this.props.meijuList}></MeijuList>
            </div>
        )
    }
}

export default connect(
    state => ({meijuList: state.meijuList}),
    {getMeijuList}
)(Recommend);